import { Component, Input, OnInit } from '@angular/core';

export enum SplashScreenStatus {
    Error = 'error',
    Informative = 'info',
    Loading = 'loading'
}

export enum SplashScreenButtonStyle {
    Normal = 'normal',
    Positive = 'positive'
}

export enum SplashScreenPosition {
    Fixed = 'fixed',
    Absolute = 'absolute'
}

export interface SplashScreenButton {
    style: SplashScreenButtonStyle,
    content: string,
    action?: () => {}
}

export interface SplashScreenConfig {
    position: SplashScreenPosition,
    message: {
        scopeName: string,
        label: string,
        params?: any
    },
    visibility: boolean,
    status: SplashScreenStatus,
    buttons?: SplashScreenButton[]
}

@Component({
    selector: 'splash-screen',
    templateUrl: './splash-screen.component.html',
    styleUrls: ['./splash-screen.component.sass']
})
export class SplashScreenComponent implements OnInit {

    SplashScreenStatus = SplashScreenStatus;

    @Input() position?: SplashScreenPosition;

    @Input() message?: {
        scopeName: string,
        label: string,
        params?: any
    };

    @Input() visibility?: boolean;

    @Input()
    status: SplashScreenStatus;

    @Input() buttons?: SplashScreenButton[];

    constructor() {
        this.message = {
            scopeName: '',
            label: ''
        };
        this.visibility = false;
        this.status = SplashScreenStatus.Loading;
        this.buttons = [];
    }

    ngOnInit() { }

}
