import { Component, OnInit } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { BsModalRef } from 'ngx-bootstrap/modal';

export interface AlertBoxButton {
    label: string,
    action?: () => void
}

export interface AlertBoxMessage {
    scopeName: string,
    label: string,
    params?: any
}

export enum AlertBoxStyle {
    Informative = 0, 
    Warning = 1,
    Error = 2
}

@Component({
    selector: 'modal-content',
    templateUrl: './alert-box.component.html',
    styleUrls: ['./alert-box.component.sass']
})
export class AlertBoxComponent implements OnInit {

    readonly AlertBoxStyle = AlertBoxStyle;

    style: AlertBoxStyle;
    buttons: AlertBoxButton[];
    message: AlertBoxMessage;

    constructor(public bsModalRef: BsModalRef,
        private _translate: TranslocoService) { }

    ngOnInit() { }

    onActionButtonClick(action: () => {}) {
        action && action();
        this.bsModalRef.hide();
    }
}
