import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  {
    path: 'landing',
    loadChildren: () => import('./portal/landing/landing.module').then(m => m.LandingModule)
  },
  {
    path: 'registration',
    loadChildren: () => import('./portal/registration/registration.module').then(m => m.RegistrationModule)
  },
  {
    path: 'appointment',
    loadChildren: () => import('./portal/appointment/appointment.module').then(m => m.AppointmentModule)
  },
  {
    path: '',
    redirectTo: 'landing',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
