import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { BookingStatus } from '../../model/BookingStatus';
import { Month } from '../../model/Month';
import { Schedule } from '../../model/Schedule';
import { ScheduleService } from '../../service/schedule/schedule.service';
import { RESULT_CODE_SUCCESS } from '../../shared/constant';

@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.sass']
})
export class CalendarComponent implements OnInit{
  scopeName = 'portal.common.calendar';

  readonly BookingStatus = BookingStatus;

  readonly months: Month[] = [];

  @Output()
  loadSchedulesStarted = new EventEmitter();

  @Output()
  loadSchedulesCompleted = new EventEmitter<string>();

  constructor(
    private readonly _scheduleService: ScheduleService) {

    _scheduleService.schedulesUpdated.subscribe(() => {
      this.refreshCalendar();
    });
  }

  ngOnInit() {
    this.refreshCalendar();

    if (this._scheduleService.selectedSchedule && this._scheduleService.selectedTimeslot) {
      this.loadSchedulesStarted.emit();

      this._scheduleService.getSchedules({ appointmentDate: this._scheduleService.selectedSchedule.appointmentDate }).subscribe({
        next: () => {
          this.loadSchedulesCompleted.emit(RESULT_CODE_SUCCESS);
        },
        error: (errorCode: string) => {
          this.loadSchedulesCompleted.emit(errorCode);
        }
      });
    }
  }

  onAppointmentDateSelected(selectedSchedule: Schedule) {
    if (selectedSchedule.status === this.BookingStatus.Available) {
      this.loadSchedulesStarted.emit();

      this._scheduleService.getSchedules({ appointmentDate: selectedSchedule.appointmentDate }).subscribe({
        next: () => {
          this.loadSchedulesCompleted.emit(RESULT_CODE_SUCCESS);
        },
        error: (errorCode: string) => {
          this.loadSchedulesCompleted.emit(errorCode);
        }
      });
    }
  }

  toDate(timestamp: number) {
    return new Date(timestamp);
  }

  getArray(size: number) {
    return Array(size).fill(0);
  }

  private refreshCalendar() {
    this.months.length = 0;

    for (const schedule of this._scheduleService.schedules) {
      let appointmentDate = new Date(schedule.appointmentDate);
      let month = appointmentDate.getMonth();

      if (this.months.length === 0) {
        this.months.push({
          month: month,
          year: appointmentDate.getFullYear(),
          dates: []
        });
      } else if (this.months[this.months.length - 1].month != month) {
        this.months.push({
          month: month,
          year: appointmentDate.getFullYear(),
          dates: []
        });
      }

      this.months[this.months.length - 1].dates.push(schedule);
    }
  }
}
