import { Component, Input } from '@angular/core';
import { ControlContainer, FormControl } from '@angular/forms';
import { MasterFormGroupDirective } from '../directive/master-form-group.directive';

@Component({
  selector: 'error-msg',
  template: `
<div class="error-msg-container" *ngIf="formField.errors" [hidden]="!isShown()">
  <ng-container *transloco="let t; scope: scopeName + '.ErrorMsg'; read: scopeName + '.ErrorMsg';">
    <i class="fa fa-exclamation-triangle"></i>
    <span class="ml-2">{{ t(formField['errorMessage']) }}</span>
  </ng-container>
</div>
  `,
  styleUrls: ['./error-msg.component.sass'],
  viewProviders: [{ provide: ControlContainer, useExisting: MasterFormGroupDirective }]
})
export class ErrorMsgComponent {

  @Input()
  scopeName: string;

  @Input()
  formField: FormControl;

  constructor(private formRef: MasterFormGroupDirective) { }

  isShown(): boolean {
    if (this.formField.errors && (this.formRef?.submitted || (this.formField.touched && this.formField.dirty) || this.formField.touched)) {
      return true;
    }

    return false;
  }
}
