import { ModuleWithProviders, NgModule } from '@angular/core';
import { PortalCommonModule } from '../common/portal-common.module';
import { HeartbeatService, HeartbeatServiceConfig } from './heartbeat.service';

@NgModule({
    declarations: [ ],
    imports: [
        PortalCommonModule
    ],
    providers: [ HeartbeatService ]
})
export class HeartbeatModule {
    constructor() { }

    static forRoot(config: HeartbeatServiceConfig): ModuleWithProviders {
        return {
            ngModule: HeartbeatModule,
            providers: [
                { provide: HeartbeatServiceConfig, useValue: config }
            ]
        };
    }
}
