import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { BookingStatus } from '../../model/BookingStatus';
import { Meridiem } from '../../model/Meridiem';
import { Timeslot } from '../../model/timeslot';
import { ScheduleService } from '../../service/schedule/schedule.service';
import { RESULT_CODE_SUCCESS } from '../../shared/constant';

@Component({
    selector: 'app-timeslot',
    templateUrl: './timeslot.component.html',
    styleUrls: ['./timeslot.component.sass']
})
export class TimeslotComponent implements OnInit {
    scopeName = 'portal.common.timeslot';

    readonly BookingStatus = BookingStatus;
    readonly Meridiem = Meridiem;

    readonly amTimeslots: Timeslot[] = [];
    readonly pmTimeslots: Timeslot[] = [];

    @Output()
    loadTimeslotStarted = new EventEmitter();

    @Output()
    loadTimeslotCompleted = new EventEmitter<string>();

    constructor(
        private readonly _scheduleService: ScheduleService) {

        _scheduleService.schedulesUpdated.subscribe(() => {
            this.refreshTimeslots();
        });
    }

    ngOnInit() {
        this.refreshTimeslots();
    }

    onAppointmentTimeslotSelected(selectedTimeslot: Timeslot) {
        if (selectedTimeslot.status === this.BookingStatus.Available) {
            this.loadTimeslotStarted.emit();

            this._scheduleService.acquireTimeSlot({
                appointmentDate: this._scheduleService.selectedSchedule.appointmentDate,
                slotId: selectedTimeslot.slotId
            }).subscribe({
                next: () => {
                    this.loadTimeslotCompleted.emit(RESULT_CODE_SUCCESS);
                },
                error: (errorCode: string) => {
                    this.loadTimeslotCompleted.emit(errorCode);
                }
            })
        }
    }

    getFormattedTime(timeValue: number) {
        if (timeValue > 1200) {
            timeValue -= 1200;
        }
        let formattedTime = timeValue.toString().length === 4 ? timeValue.toString() : '0' + timeValue;
        return formattedTime.substring(0, 2) + ':' + formattedTime.substring(2, 4);
    }

    private refreshTimeslots() {
        this.amTimeslots.length = 0;
        this.pmTimeslots.length = 0;

        this._scheduleService.timeSlots.filter(x => x.startTime < 1200).forEach(x => this.amTimeslots.push(x));
        this._scheduleService.timeSlots.filter(x => x.startTime >= 1200).forEach(x => this.pmTimeslots.push(x));
    }
}
