import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { ComponentFactoryResolver, Injectable, Optional } from '@angular/core';
import { Router } from '@angular/router';
import { Idle, InterruptSource } from '@ng-idle/core';
import { Keepalive } from '@ng-idle/keepalive';
import { ConfigService } from '@ngx-config/core';
import { BsModalRef } from 'ngx-bootstrap/modal/public_api';
import { AlertBoxStyle } from '../common/alert-box/alert-box.component';
import { AlertBoxService } from '../service/alert-box.service';
import { PortalService } from '../service/portal.service';
import { HEADERS_AJAX_JSON_REQUEST, RESULT_CODE_SUCCESS } from '../shared/constant';
import { ResultCodeResponse } from '../shared/response/ResultCodeResponse';

export class HeartbeatServiceConfig {
    idleTimeout: number = 0;
    timeoutPeriod: number = 0;
    interrupts: InterruptSource[] = [];
    keepaliveInterval: number = 0;
}

export interface HeartbeatInternalState {
    started: boolean,
    timeout: {
        countdown: number,
        alertBoxRef: BsModalRef
    }
}

@Injectable({
    providedIn: 'root'
})
export class HeartbeatService {
    private _config: HeartbeatServiceConfig;
    private _componentFactoryResolver: ComponentFactoryResolver;

    private _state: HeartbeatInternalState = {
        started: false,
        timeout: {
            alertBoxRef: null,
            countdown: 0
        }
    };

    constructor(
        readonly _keepalive: Keepalive,
        private readonly _idle: Idle,
        private readonly _httpClient: HttpClient,
        private readonly _systemConfig: ConfigService,
        private readonly _alertBoxService: AlertBoxService,
        private readonly _router: Router,
        private readonly _portalService: PortalService,
        @Optional() readonly config?: HeartbeatServiceConfig,
    ) {
        if (config) {
            this._config = config;
        } else {
            this._config = new HeartbeatServiceConfig();
        }

        _idle.setIdle(this._config.idleTimeout);
        _idle.setTimeout(this._config.timeoutPeriod);
        _idle.setInterrupts(this._config.interrupts);

        _keepalive.interval(this._config.keepaliveInterval);
        _keepalive.onPing.subscribe(() => { this.sendHeartbeat() });

        _idle.onIdleStart.subscribe(()=>{ console.log('idle started'); }); // TODO: remove this line
        _idle.onIdleEnd.subscribe(()=>{
            console.log('idle ended');

            // add a little deplay so that the alert box will not be 
            // hidden before it has been shown
            setTimeout(() => {
                this._state.timeout.alertBoxRef?.hide();
                this._state.timeout.alertBoxRef = null;
            }, 500);
            
        });
        _idle.onTimeoutWarning.subscribe((countdown) => { 
            this.onTimeoutStart(countdown);
        });
        _idle.onTimeout.subscribe(() => {
            this.onTimeout();
        });
    }

    start() {
        if (!this._state.started) {
            this._idle.watch();
            this._state.started = true;
        }
    }

    stop() {
        this._idle.stop();
        this.resetState();
    }

    register(componentFactoryResolver: ComponentFactoryResolver) {
        this._componentFactoryResolver = componentFactoryResolver;
    }

    private resetState() {
        this._state.started = false;
        this._state.timeout.alertBoxRef = null;
        this._state.timeout.countdown = 0;
    }

    private goToTimeoutPage() {
        this._router.navigate([ '/landing/timeout' ]);
    }

    private onTimeoutStart(countdown: number) {
        this._state.timeout.countdown = countdown;
        // hide the last alert box if present
        this._state.timeout.alertBoxRef?.hide();
        this._state.timeout.alertBoxRef = this._alertBoxService.show(this._componentFactoryResolver, {
            style: AlertBoxStyle.Warning,
            buttons: [ ],
            message: {
                scopeName: 'portal.Heartbeat',
                label: 'lbl.timeoutCountDown',
                params: { countdown: this._state.timeout.countdown }
            }
        });
    }

    private onTimeout() {
        console.log('timeout!');

        this._state.timeout.alertBoxRef?.hide();

        this.resetState();

        this._portalService
            .terminateUserSession()
            .subscribe({
                next: () => {
                    this.promptTimeoutErrorMessage();
                },
                error: () => {
                    // even the call returned with error, user session on client side will be discarded
                    // i.e. JWT in session storage removed
                    this.promptTimeoutErrorMessage();
                }
            });
    }

    private promptTimeoutErrorMessage() {
        this._alertBoxService.show(this._componentFactoryResolver, {
            style: AlertBoxStyle.Error,
            buttons: [ 
                { 
                    label: 'close', 
                    action: () => {
                        this.goToTimeoutPage();
                    } 
                } 
            ],
            message: {
                scopeName: 'portal.Heartbeat',
                label: 'lbl.timeout',
                params: { countdown: this._state.timeout.countdown }
            }
        });
    }

    private sendHeartbeat() {
        console.log('send heartbeat');
        this._httpClient.post(
            this._systemConfig.getSettings<string>('system.hostApi') + '/services/PortalManagement/Portal/sendHeartbeat',
            null,
            {
                headers: HEADERS_AJAX_JSON_REQUEST
            }
        ).subscribe({
            next: (response: ResultCodeResponse) => {
                if (response.resultCode === RESULT_CODE_SUCCESS) {
                    console.log('heartbeat - 0000');
                } else {
                    console.log('heartbeat - result code error');
                    this.goToTimeoutPage();
                }
            },
            error: (error: HttpErrorResponse) => {
                console.log('heartbeat - http error');
                this.goToTimeoutPage();
            }
        });
    }
}