import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { SESSION_STORAGE, WebStorageService } from 'ngx-webstorage-service';
import { Observable } from 'rxjs';
import { SESSION_DATA_KEY_JWT } from '../shared/constant';

@Injectable()
export class JwtInterceptor implements HttpInterceptor {

    constructor(
        @Inject(SESSION_STORAGE) private _sessionStorage: WebStorageService) {
    }

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        let token: string = this._sessionStorage.get(SESSION_DATA_KEY_JWT);
        if (token !== null) {
            let headers = request.headers.set('Authorization', `Bearer ${token}`)
            request = request.clone({ headers: headers });
        }

        return next.handle(request);
    }
}