import { Component, EventEmitter, NgModuleRef, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { RxFormBuilder, RxwebValidators } from '@rxweb/reactive-form-validators';
import { Captcha } from '../../model/Captcha';
import { PortalService } from '../../service/portal.service';
import { AlertBoxStyle } from '../alert-box/alert-box.component';
import { AlertBoxService } from '../../service/alert-box.service';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
    selector: 'app-captcha',
    template: `
<ng-container *transloco="let t1; scope: 'portal.Common.Captcha'; read: 'portal.Common.Captcha'">
    <div class="form-row my-1" [formGroup]="captchaForm">
        <label class="col-md-5 col-form-label">{{ t1('msg') }}</label>
        <div class="col-md-7">
            <input class="form-control input-captcha my-1" type="text" id="captcha" name="captcha" formControlName="captcha"/>
            <div class="form-row">
                <div class="col-auto form-inline">
                    <div class="input-group">
                        <img class="form-control img-captcha" [src]="domSanitizer.bypassSecurityTrustUrl(captcha.image)" />
                        <div class="input-group-append">
                            <button type="button" class="btn btn-outline-secondary" (click)="refreshCaptcha()"><i class="fas fa-redo" aria-hidden="true"></i></button>
                        </div>
                    </div>
                </div>
            </div>
            <error-msg class="col-12" [scopeName]="scopeName" [formField]="captchaForm.controls.captcha"></error-msg>
        </div>
    </div>
</ng-container>
    `,
    styleUrls: ['./captcha.component.sass'],
    providers: [AlertBoxService]
})
export class CaptchaComponent implements OnInit {
    captchaForm: FormGroup;
    scopeName = 'portal.Common.Captcha';

    captcha: Captcha;

    @Output()
    loadStarted = new EventEmitter();

    @Output()
    loadCompleted = new EventEmitter<boolean>();

    private readonly _placeholderImage = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAIAAACQd1PeAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAJcEhZcwAADsMAAA7DAcdvqGQAAAAMSURBVBhXY7hw4QIABOQCcaizOWcAAAAASUVORK5CYII=';

    constructor(
        private readonly _formBuilder: RxFormBuilder,
        private readonly _portalService: PortalService,
        private readonly _alertBoxService: AlertBoxService,
        private readonly _ngModuleRef: NgModuleRef<CaptchaComponent>,
        public domSanitizer: DomSanitizer) {

    }
    ngOnInit(): void {
        this.captchaForm = this._formBuilder.group({
            captcha: ['', RxwebValidators.required({ message: '0018' })]
        });

        this.captcha = {
            image: this._placeholderImage,
            sessionId: ''
        };

        this.refreshCaptcha();
    }

    refreshCaptcha(): void {
        this.loadStarted.emit();

        this.captcha.image = this._placeholderImage;

        this._portalService.getCaptcha().subscribe({
            next: (captcha: Captcha) => {
                this.captcha = captcha;
                this.loadCompleted.emit(true);
            },
            error: (errorCode: string) => {
                this.loadCompleted.emit(false);

                this._alertBoxService.show(this._ngModuleRef.componentFactoryResolver, {
                    style: AlertBoxStyle.Error,
                    buttons: [{ label: 'close' }],
                    message: {
                        scopeName: this.scopeName + '.ErrorMsg',
                        label: errorCode
                    }
                });
            }
        });
    }
}
