import { Directive, ElementRef, Input } from '@angular/core';
import { FormGroup, FormGroupDirective } from '@angular/forms';

@Directive({ selector: '[masterFormGroup]'})
export class MasterFormGroupDirective extends FormGroupDirective {

    @Input('masterFormGroup')
    form: FormGroup;

    constructor(
        private readonly _elementRef: ElementRef) {

        super(null, null);

        this.ngSubmit.subscribe(_ => {
            this._elementRef.nativeElement.classList.add("ng-submitted");
        })
    }
}