import { APP_BASE_HREF, PlatformLocation } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Inject, Injectable, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { Translation, translocoConfig, TranslocoConfig, TranslocoLoader, TranslocoMissingHandler, TranslocoModule, TRANSLOCO_CONFIG, TRANSLOCO_LOADER, TRANSLOCO_LOADING_TEMPLATE, TRANSLOCO_MISSING_HANDLER } from '@ngneat/transloco';
import { environment } from '../environments/environment';
import { LanguageService } from './portal/service/language.service';

@Injectable({ providedIn: 'root' })
export class TranslocoHttpLoader implements TranslocoLoader {
  constructor(
    private readonly _httpClient: HttpClient,
    @Inject(APP_BASE_HREF) private readonly _baseHref: string) {}

  getTranslation(lang: string) {
    console.log(this._baseHref);
    return this._httpClient.get<Translation>(`${window.location.origin}/${this._baseHref}/assets/i18n/${lang}.json`);
  }
}

@Injectable({ providedIn: 'root' })
export class CustomHandler implements TranslocoMissingHandler {
  constructor(
    private readonly _translate: LanguageService
  ) {

  }

  handle(key: string, config: TranslocoConfig) {
    if (key.indexOf('.ErrorMsg.') > 0) {
      let lang = this._translate.getLanguage();

      let errorCode = key.split('.').pop();

      if (lang === 'en') {
        return `System busy [E-5${errorCode}]`;
      } else {
        return ` [E-5${errorCode}]`;
      }
    } else {
      return key;
    }
  }
}

@NgModule({
  imports: [ BrowserModule ],
  exports: [ TranslocoModule ],
  providers: [
    {
      provide: TRANSLOCO_CONFIG,
      useValue: translocoConfig({
        availableLangs: ['en', 'zh'],
        defaultLang: 'en',
        fallbackLang: 'en',
        reRenderOnLangChange: true,
        prodMode: environment.production,
      })
    },
    { provide: TRANSLOCO_LOADER, useClass: TranslocoHttpLoader },
    { provide: TRANSLOCO_LOADING_TEMPLATE, useValue: ' ' },
    { provide: TRANSLOCO_MISSING_HANDLER, useClass: CustomHandler },
    {
        provide: APP_BASE_HREF,
        useFactory: (s: PlatformLocation) => s.getBaseHrefFromDOM(),
        deps: [PlatformLocation]
    }
  ]
})
export class TranslocoRootModule {}
