import { ComponentFactoryResolver, Injectable } from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { PortalCommonModule } from '../common/portal-common.module';
import { AlertBoxButton, AlertBoxComponent, AlertBoxMessage, AlertBoxStyle } from '../common/alert-box/alert-box.component';

export interface AlertBoxConfig {
    style: AlertBoxStyle,
    buttons: AlertBoxButton[],
    message: AlertBoxMessage
}

@Injectable({
    providedIn: 'root'
})
export class AlertBoxService {
    constructor(private _bsModalService: BsModalService) { }

    show(componentFactoryResolver: ComponentFactoryResolver, config: AlertBoxConfig): BsModalRef {
        let modalOption = {
            keyboard: false,
            initialState: {
                style: config.style,
                buttons: config.buttons,
                message: undefined
            }
        };

        modalOption.initialState.message = config.message;

        return this._bsModalService.show(
            componentFactoryResolver.resolveComponentFactory(AlertBoxComponent).componentType,
            modalOption);
    }
}