import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { DEFAULT_INTERRUPTSOURCES } from '@ng-idle/core';
import { NgIdleKeepaliveModule } from '@ng-idle/keepalive';
import { ConfigLoader, ConfigModule, ConfigStaticLoader } from '@ngx-config/core';
import { ModalModule } from 'ngx-bootstrap/modal';
import { CookieService } from 'ngx-cookie-service';
import { StorageServiceModule } from 'ngx-webstorage-service';
import { CoalescingComponentFactoryResolver } from 'src/app/portal/service/coalescing-component-factory-resolver.service';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HeartbeatModule } from './portal/heartbeat/heartbeat.module';
import { BackendHttpInterceptor } from './portal/interceptor/backend.interceptor';
import { JwtInterceptor } from './portal/interceptor/jwt.interceptor';
import { LanguageService } from './portal/service/language.service';
import { TranslocoRootModule } from './transloco-root.module';

export function configFactory(): ConfigLoader {
  return new ConfigStaticLoader({
    "system": {
      "hostApi": "../brbs_api"
    }
  });
}

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    AppRoutingModule,
    StorageServiceModule,

    ConfigModule.forRoot({
      provide: ConfigLoader,
      useFactory: (configFactory)
    }),

    ModalModule.forRoot(),
    NgIdleKeepaliveModule.forRoot(),
    HeartbeatModule.forRoot({
      idleTimeout: 60,
      timeoutPeriod: 60,
      interrupts: DEFAULT_INTERRUPTSOURCES,
      keepaliveInterval: 10
    }),
    TranslocoRootModule
  ],
  providers: [
    { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: BackendHttpInterceptor, multi: true },
    CookieService,
    CoalescingComponentFactoryResolver,
    LanguageService
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
  constructor(coalescingResolver: CoalescingComponentFactoryResolver) {
    coalescingResolver.init();
  }
}
