import { Injectable } from '@angular/core';
import { TranslocoService } from '@ngneat/transloco';
import { CookieService } from 'ngx-cookie-service';

const DEFAULT_LANGUAGE = 'en';
const LANGUAGE_COOKIE_KEY = 'portal.common.preferedLanguage;'

@Injectable({
    providedIn: 'root'
})
export class LanguageService {

    private _language: string;
    private _translate: TranslocoService;

    constructor(private _cookieService: CookieService) { }

    initialize() {
        let lang = this._cookieService.get(LANGUAGE_COOKIE_KEY);
        if (!lang) {
            lang = DEFAULT_LANGUAGE;
        }

        this._language = lang;
    }

    register(translate: TranslocoService) {
        this._translate = translate;
        this._translate.setAvailableLangs(['en', 'zh']);
        this._translate.setActiveLang(this._language);
    }

    setLanguage(lang: string) {
        this._translate.setActiveLang(lang);
        this._cookieService.set(LANGUAGE_COOKIE_KEY, lang);
    }

    getLanguage() : string {
        return this._language;
    }
}