import { Component, Input, OnInit } from '@angular/core';
import { Observable } from 'rxjs';

export enum CommandBarPosition {
  Fixed = 'fixed',
  Bottom = 'bottom'
}

export enum CommandBarButtonStyle {
  Normal = 'normal',
  Positive = 'positive'
}

export enum CommandBarButtonType {
  GeneralButton = 'button',
  SubmitButton = 'submit'
}

export class CommandBarButton {
  content: {
    label: string,
    scopeName: string
  };
  style: CommandBarButtonStyle;
  type?: CommandBarButtonType = CommandBarButtonType.GeneralButton;
  hidden: boolean;
  disabled: boolean;
  action: () => Observable<any>;
  actionArgs?: any;
}

@Component({
  selector: 'command-bar',
  templateUrl: './command-bar.component.html',
  styleUrls: ['./command-bar.component.sass']
})
export class CommandBarComponent implements OnInit {

  @Input() buttons: CommandBarButton[];

  @Input() position?: CommandBarPosition;   // 'fixed' or 'bottom'

  constructor() {
    this.buttons = [];
    this.position = CommandBarPosition.Fixed;
  }

  ngOnInit() { }

  onButtonClick(button: CommandBarButton) {
    if (button.action) {
      button.disabled = true;
      button.action.apply(this, button.actionArgs).subscribe({
        next: () => button.disabled = false
      });
    }
  }

}
