import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { EventEmitter, Injectable } from '@angular/core';
import { ConfigService } from '@ngx-config/core';
import { Observable } from 'rxjs';
import { BookingStatus } from '../../model/BookingStatus';
import { Schedule } from '../../model/Schedule';
import { Timeslot } from '../../model/timeslot';
import { HEADERS_AJAX_JSON_REQUEST, RESULT_CODE_SUCCESS } from '../../shared/constant';
import { AcquireTimeSlotRequest } from './request/AcquireTimeSlotRequest';
import { GetScheduleRequest } from './request/GetScheduleRequest';
import { AcquireTimeSlotResponse } from './response/AcquireTimeSlotResponse';
import { GetBookingPeriodResponse } from './response/GetBookingPeriodResponse';
import { GetScheduleResponse } from './response/GetScheduleResponse';

const RESULT_CODE_DATE_NO_LONGER_AVAILABLE = '0027';
const RESULT_CODE_TIMESLOT_NO_LONGER_AVAILABLE = '0028';

@Injectable()
export class ScheduleService {

    readonly schedules: Schedule[] = [];
    readonly timeSlots: Timeslot[] = [];

    selectedSchedule: Schedule;
    selectedTimeslot: Timeslot;

    readonly schedulesUpdated = new EventEmitter();
    readonly timeslotsUpdated = new EventEmitter();

    constructor(
        private readonly _systemConfig: ConfigService,
        private readonly _httpClient: HttpClient) {

        this.selectedSchedule = null;
        this.selectedTimeslot = null;
    }

    reset() {
        this.schedules.length = 0;
        this.timeSlots.length = 0;

        this.selectedSchedule = null;
        this.selectedTimeslot = null;

        this.schedulesUpdated.emit();
        this.timeslotsUpdated.emit();
    }

    getBookingPeriod(): Observable<number> {
        const observable = new Observable<number>((observer) => {
            this._httpClient.post(
                this._systemConfig.getSettings<string>('system.hostApi') + '/services/AppointmentManagement/Schedule/getBookingPeriod',
                {},
                {
                    headers: HEADERS_AJAX_JSON_REQUEST
                }
            ).subscribe({
                next: (response: GetBookingPeriodResponse) => {
                    if (response.resultCode === RESULT_CODE_SUCCESS) {
                        observer.next(response.bookingPeriod);
                    } else {
                        observer.error(response.resultCode);
                    }
                },
                error: (error: HttpErrorResponse) => {
                    observer.error('' + error.status);
                }
            });
        });
        return observable;

    }

    setSchedules(schedules: Schedule[]): void {
        this.schedules.length = 0;
        schedules.forEach(x => this.schedules.push(x));

        this.schedulesUpdated.emit();
    }

    getSchedules(request: GetScheduleRequest): Observable<unknown> {
        const observable = new Observable<unknown>((observer) => {
            this._httpClient.post(
                this._systemConfig.getSettings<string>('system.hostApi') + '/services/AppointmentManagement/Schedule/getSchedule',
                request,
                {
                    headers: HEADERS_AJAX_JSON_REQUEST
                }
            ).subscribe({
                next: (response: GetScheduleResponse) => {
                    if (response.resultCode === RESULT_CODE_SUCCESS) {
                        // clear previous selected schedule and timeslot when the user selected another appointment date
                        if (request?.appointmentDate != this.selectedSchedule?.appointmentDate) {
                            this.selectedSchedule = null;
                            this.selectedTimeslot = null;

                            // update the booking status with latest selected schedule and timeslot
                            this.updateBookingStatus(response.schedules, request.appointmentDate, null);
                        } else {
                            // update the booking status with latest selected schedule and timeslot
                            this.updateBookingStatus(response.schedules, this.selectedSchedule?.appointmentDate, this.selectedTimeslot?.slotId);
                        }

                        observer.next();
                    } else if (response.resultCode === RESULT_CODE_DATE_NO_LONGER_AVAILABLE) {
                        // update the booking status with previous selected schedule and timeslot
                        this.updateBookingStatus(response.schedules, this.selectedSchedule?.appointmentDate, this.selectedTimeslot?.slotId);
                        observer.error(response.resultCode);
                    } else {
                        observer.error(response.resultCode);
                    }
                },
                error: (error: HttpErrorResponse) => {
                    observer.error('' + error.status);
                }
            });
        });
        return observable;
    }

    acquireTimeSlot(request: AcquireTimeSlotRequest): Observable<unknown> {
        const observable = new Observable<unknown>((observer) => {
            this._httpClient.post(
                this._systemConfig.getSettings<string>('system.hostApi') + '/services/AppointmentManagement/Schedule/acquireTimeSlot',
                request,
                {
                    headers: HEADERS_AJAX_JSON_REQUEST
                }
            ).subscribe({
                next: (response: AcquireTimeSlotResponse) => {
                    if (response.resultCode === RESULT_CODE_SUCCESS) {
                        // update the booking status with latest selected schedule and timeslot
                        this.updateBookingStatus(response.schedules, request.appointmentDate, request.slotId);
                        observer.next();
                    } else if (response.resultCode === RESULT_CODE_DATE_NO_LONGER_AVAILABLE || response.resultCode === RESULT_CODE_TIMESLOT_NO_LONGER_AVAILABLE) {
                        // update the booking status with previous selected schedule and timeslot
                        this.updateBookingStatus(response.schedules, this.selectedSchedule?.appointmentDate, this.selectedTimeslot?.slotId);
                        observer.error(response.resultCode);
                    } else {
                        observer.error(response.resultCode);
                    }
                },
                error: (error: HttpErrorResponse) => {
                    observer.error('' + error.status);
                }
            });
        });
        return observable;
    }

    private updateBookingStatus(latestSchedule: Schedule[], selectedAppointmentDate: number, selectedTimeslotId: number) {
        this.schedules.length = 0;
        latestSchedule.forEach(x => {
            // mark the schedule with date = requested date as "Selected"
            // for other schedules, status reset automatically when schedules updated
            if (selectedAppointmentDate && x.appointmentDate === selectedAppointmentDate) {
                this.selectedSchedule = x;
                x.status = BookingStatus.Selected;

                // update time slots only when the seleted schedule contains an updated time slot list
                if (x.timeSlots.length > 0) {
                    this.timeSlots.length = 0;

                    // update time slots
                    x.timeSlots.forEach(y => {
                        // mark the timeslot with slotId = requested slotId as "Selected"
                        // for other timeSlots, status reset automatically when timeSlots updated
                        if (selectedTimeslotId && selectedTimeslotId === y.slotId) {
                            this.selectedTimeslot = y;
                            y.status = BookingStatus.Selected;
                        }
                        this.timeSlots.push(y)
                    });

                    this.timeslotsUpdated.emit();
                }
            }
            this.schedules.push(x);
        });

        this.schedulesUpdated.emit();
    }
}
