import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { TranslocoEvents, TranslocoService } from '@ngneat/transloco';
import { ConfigService } from '@ngx-config/core';
import { SESSION_STORAGE, WebStorageService } from 'ngx-webstorage-service';
import { Observable } from 'rxjs';
import { filter } from 'rxjs/operators';
import { Captcha } from '..//model/Captcha';
import { HEADERS_AJAX_JSON_REQUEST, RESULT_CODE_SUCCESS, SESSION_DATA_KEY_JWT } from '../shared/constant';
import { ResultCodeResponse } from '../shared/response/ResultCodeResponse';
import { GetCaptchaResponse } from './response/GetCaptchaResponse';
import { HealthCheckResponse } from './response/HealthCheckResponse';

@Injectable({
    providedIn: 'root'
})
export class PortalService {

    private _isTermsAccepted = false;

    constructor(
        private readonly _title: Title,
        private readonly _systemConfig: ConfigService,
        private readonly _translate: TranslocoService,
        @Inject(SESSION_STORAGE) private readonly _sessionStorage: WebStorageService,
        private readonly _httpClient: HttpClient) {
    }

    setPageTitle(title: string) {
        this._translate.events$.pipe(
            filter(e => e.type === 'translationLoadSuccess')
        ).subscribe((eventArg: TranslocoEvents) => {
            this._title.setTitle(this._translate.translate(title));
        });

        this._translate.langChanges$.subscribe((_) => {
            this._title.setTitle(this._translate.translate(title));
        });
    }

    reg(browserName: string, userAgent: string){
        var re = browserName + '(\\d+)\.(\\d)';
        var reg = new RegExp(re, 'g');
        var version = reg.exec(userAgent);
        return version[1] + '.' + version[2];
    }

    getBrowserInfo() {
        const userAgent = window.navigator.userAgent;
        let browserName = '';
        let browserVersion = '';

        if (userAgent.indexOf('rv:') != -1 && userAgent.indexOf('Trident/') != -1) {
            browserName = 'Internet Explorer';
            browserVersion = this.reg('rv:', userAgent);
        } else if (userAgent.indexOf('Firefox/') != -1 && userAgent.indexOf('SeaMonkey/') == -1) {
            browserName = 'Firefox';
            browserVersion = this.reg('Firefox/', userAgent);
        } else if (userAgent.indexOf('Version/') != -1 || userAgent.indexOf('Safari/') != -1  && (userAgent.indexOf('Chrome/') == -1 && userAgent.indexOf('Chromium/') == -1)) {
            browserName	 = 'Safari';
        } else if (userAgent.indexOf('Chrome/') != -1 && userAgent.indexOf('Chromium/') == -1) {
            browserName = 'Chrome';
            browserVersion = this.reg('Chrome/', userAgent);
        } else if (userAgent.indexOf('CriOS/') != -1) {
            browserName = 'Chrome';
            browserVersion = this.reg('CriOS/', userAgent);
        }
        return [browserName, browserVersion];
    }

    getCaptcha(): Observable<Captcha> {
        const browserInfo = this.getBrowserInfo();

        const observable = new Observable<Captcha>((observer) => {
            this._httpClient.post(
                this._systemConfig.getSettings<string>('system.hostApi') + '/services/PortalManagement/Portal/getCaptcha',
                {
                    browserName: browserInfo[0],
                    browserVersion: browserInfo[1]},
                {
                    headers: HEADERS_AJAX_JSON_REQUEST
                }
            ).subscribe({
                next: (response: GetCaptchaResponse) => {
                    if (response.resultCode === RESULT_CODE_SUCCESS) {
                        let captcha = {
                            sessionId: response.sessionId,
                            image: response.captchaImageUri
                        };
                        observer.next(captcha);
                    } else {
                        observer.error(response.resultCode);
                    }
                },
                error: (error: HttpErrorResponse) => {
                    observer.error('' + error.status);
                }
            });
        });
        return observable;
    }

    healthCheck(): Observable<unknown> {
        const observable = new Observable<unknown>((observer) => {
            this._httpClient.post(
                this._systemConfig.getSettings<string>('system.hostApi') + '/services/PortalManagement/Portal/healthCheck',
                {},
                {
                    headers: HEADERS_AJAX_JSON_REQUEST
                }
            ).subscribe({
                next: (response: HealthCheckResponse) => {
                    if (response.resultCode === RESULT_CODE_SUCCESS) {
                        observer.next({announment: response.announcement, maintenance: response.maintenance});
                    } else {
                        observer.error(response.resultCode);
                    }
                },
                error: (error: HttpErrorResponse) => {
                    observer.error('' + error.status);
                }
            });
        });
        return observable;
    }

    terminateUserSession(): Observable<any> {
        let observable = new Observable<any>((observer) => {
            this._httpClient.post(
                this._systemConfig.getSettings<string>('system.hostApi') + '/services/PortalManagement/Portal/terminateUserSession',
                null,
                {
                    headers: HEADERS_AJAX_JSON_REQUEST
                }
            ).subscribe({
                next: (response: ResultCodeResponse) => {
                    if (response.resultCode === RESULT_CODE_SUCCESS) {
                        // remove JWT from session storage after session terminated
                        this._sessionStorage.remove(SESSION_DATA_KEY_JWT);
                        observer.next();
                    } else {
                        observer.error(response.resultCode);
                    }
                },
                error: (errorResponse: HttpErrorResponse) => {
                    observer.error(errorResponse.status);
                }
            })
        });
        return observable;
    }
}